import { create } from "zustand";

const useStore = create((set) => ({
 // id: 1,
 // setId: (id) => set((state) => ({ id })),
  isMenuOpen: false,
  setMenuOpen: (isMenuOpen) => set((state) => ({ isMenuOpen })),
  header: false,
  setHeader: (header) => set((state) => ({ header })),
}));
export default useStore;
