import useStore from "../../data/Store";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion";
var jsonData = require("../../data/pages.json");

export default function Sidebar() {
  const { id, isMenuOpen } = useStore();
  const navigate = useNavigate();
  const setMenuOpen = useStore((s) => s.setMenuOpen);
  //const setId = useStore((s) => s.setId);
  const handleClick = (id) => {
    //setId(id);
    setMenuOpen(false);
    //navigate(`/catalogs/${id}`);
    document.location.href=`/catalogs/${id}`;
  };
  const goHome = () => {
    navigate("/");
    setMenuOpen(false);
  };
  const variants = {
    open: {
      y: 0,
      opacity: 1,
      transition: {
        y: { stiffness: 1000, velocity: -200 },
      },
    },
    closed: {
      y: 50,
      opacity: 0,
      transition: {
        y: { stiffness: 1000 },
      },
    },
  };
  const UlVariants = {
    open: {
      transition: { staggerChildren: 0.07, delayChildren: 0.2 },
    },
    closed: {
      transition: { staggerChildren: 0.05, staggerDirection: -1 },
    },
  };
  return (
    <motion.div
      animate={isMenuOpen ? "open" : "closed"}
      className={"side-bar " + (isMenuOpen === true ? "active" : "")}
    >
      <div className="sidebar-inner">
        <span
          className="close"
          onClick={() => {
            setMenuOpen(false);
          }}
        ></span>
        <h5> </h5>
        <motion.ul variants={UlVariants}>
          <motion.li onClick={goHome} variants={variants}>
            <span> </span>
          </motion.li>
          {jsonData.map((x) => {
            return (
              <motion.li
                key={x.id}
                variants={variants}
                className={x.id === id ? "active" : ""}
                whileHover={{ opacity: 0.9 }}
                whileTap={{ opacity: 0.85 }}
                onClick={() => {
                  handleClick(x.id);
                }}
              >
                {x.name}
              </motion.li>
            );
          })}
        </motion.ul>
      </div>
    </motion.div>
  );
}
