import slogan from "../assets/slogan.png";
import logo from "../assets/province-zanjan-logo.png";


export default function Header() {
  return (
    <div className="header">
      <div className="header-top">
        <div className="container">
          <div className="slogan">
            <div>
              <div><img src={slogan} alt="" /></div>
              <div className="left">
                <ul>
                  
                  <li><a href="https://zanjan.mcth.ir/" target="_blank" rel="noreferrer"> </a></li>
                  <li><a href="https://goo.gl/maps/pCgUhAPzv8th2idP7" target="_blank" rel="noreferrer"> </a></li>
                </ul>
              </div>
            </div>
            <img src={logo} alt="" className="logo" />
          </div>
        </div>
      </div>
    </div>
  );
}